<template>
<div>
    <div role="group">
        <b-form-input id="input-live" v-model="email" :state="nameState" aria-describedby="input-id-feedback" placeholder="Email" type="email" trim></b-form-input>
        <!-- This will only be shown if the preceding input has an invalid state -->
        <b-form-invalid-feedback id="input-id-feedback">
            Introduce tu email
        </b-form-invalid-feedback>
    </div>
    <div class="text-center">
        <b-button variant="success" @click="makeToast('success')" class="mb-2">Recuperar</b-button>
    </div>
</div>
</template>

<script>
export default {
    methods: {
        makeToast(variant = null) {
            if (this.email.length > 2) {
                console.log(this.email)
                this.axios.post('/api/chatbot/auth/request-restore.json?mail=dcorzaa@ultrasist.com.mx', {
                        mail: "dcorzaa@ultrasist.com.mx"
                    }).then((response) => {
                        console.log(response.data.succeed)
                        if (response.data.succeed) {
                            this.$bvToast.toast('Revise opciones de recuperación...' + this.email, {
                                title: `aviso`,
                                variant: 'warning',
                                toaster: 'b-toaster-bottom-center',
                                solid: true
                            });
                        }
                        console.log(response.data);
                    })
                    .catch((e) => {
                        console.log(e);
                    });
                this.email = ''
            }
        }
    },
    computed: {
        nameState() {
            return this.email.length > 2 ? true : false
        }
    },
    data() {
        return {
            email: ''
        }
    }
}
</script>
