<template>
<div>
    <b-form @submit="onSubmit"> 
     <b-input-group  key="" size="" class="mb-3" :prepend="opcionesTab">
            <b-form-input v-model="form.txt" required></b-form-input>
            <b-input-group-append>
                <b-button size="sm" text="Button" type="submit" variant="success">Agregar</b-button>
            </b-input-group-append>
        </b-input-group>
    </b-form>
</div>
</template>
<script>
export default {
    props:['opcionesTab'],
     data() {
      return {
        form: {
            txt:''
        }
      }
     },
     methods:{
      onSubmit(evt) {
        evt.preventDefault()
        this.showConfirm()
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.form.txt = ''
          })
          .catch(err => {
            // An error occurred
          })
      }
}
}
</script>