<template>
    <div>
        <b-form @submit="onSubmit" v-if="data.show">
      <b-form-group
        id="input-group-1"
        :label="data.name"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="data.form.name"          
          required
          :placeholder="data.name"
        ></b-form-input>
      </b-form-group>
      <p></p>
      <b-container class="bv-example-row">
        <b-row>
            <b-col cols="6"></b-col>
            <b-col cols="2">
                <b-button type="submit" variant="success">Actualizar</b-button>                
            </b-col>
            <b-col cols="1">
            </b-col>
            <b-col cols="2">
                <b-button @click="$bvModal.hide(data.name)">Cancelar</b-button>
            </b-col>
        </b-row>        
      </b-container>
    </b-form>
   
   
  </div>
</template>

<script>
  export default {
   props:['data'],
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
       this.showConfirm()
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.evento = ''
       
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.$bvModal.hide(data.name)
          })
          .catch(err => {
            // An error occurred
          })
      }
    }
  }


</script>