<template>
    <div>
        <b-form @submit="onSubmit"> 
            <b-input-group :prepend="prepends" class="mb-2">
            <b-form-input aria-label="ContextoEntrada" id="text-in" v-model="form.entrada" required></b-form-input>
            <b-form-input aria-label="ContextoSalida" id="text-out" v-model="form.salida" required></b-form-input>
            <b-input-group-append>
                <b-button size="sm" text="Button" type="submit" variant="success">Agregar</b-button>
            </b-input-group-append>
        </b-input-group>
        </b-form>
        
    </div>
</template>
<script>
export default {
    props:['prepends'],
    data() {
      return {
        form: {
          entrada: '',
          salida: ''
        }
      }
    },
    methods:{
      onSubmit(evt) {
        evt.preventDefault()
        this.showConfirm()
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.form.entrada = ''
            this.form.salida = ''
          })
          .catch(err => {
            // An error occurred
          })
      }
    }
}
</script>