<template>
<div>
    <b-container class="text-left">
        <b-row>
            <b-col cols="4"></b-col>
            <b-col cols="4">
                <b-card title="" :img-src='logo()' img-alt="Image" img-top tag="article" style="max-width: 20rem;" class="mb-2">
                    <b-card-text>
                        <b-form @submit="onSubmit" v-if="show">
                            <b-form-group id="input-group-1" label="ID:" label-for="input-1" description="">
                                <b-form-input id="input-1" v-model="form.id" required placeholder="" :state="nameStateID" aria-describedby="input-feedback-id" trim></b-form-input>
                                <b-form-invalid-feedback id="input-feedback-id">
                                    Introduce el ID
                                </b-form-invalid-feedback>

                            </b-form-group>
                            <b-form-group id="input-group-2" label="Contraseña:" label-for="input-2">
                                <b-form-input id="input-2" v-model="form.contra" required placeholder="" type="password" :state="nameStatePass" aria-describedby="input-feedback-pass" trim></b-form-input>
                                <b-form-invalid-feedback id="input-feedback-pass">
                                    Introduce la contraseña
                                </b-form-invalid-feedback>
                            </b-form-group>
                            <div class="text-center">
                                <b-button type="submit" variant="success">Entrar</b-button>
                                <b-button  id="popover-r" variant="white">
                                    <eva-icon name="options" animation="pulse" fill="limegreen"></eva-icon>
                                </b-button>
                                <b-popover target="popover-r" triggers="hover focus">
                                    <template v-slot:title>Recupera contraseña</template>
                                     <div class="text-center">
                                        <restorePass></restorePass>
                                    </div>
                                </b-popover>
                            </div>
                            
                            <p></p>
                            <div class="text-center">
                                <b-alert :show="dismissCountDown" dismissible fade variant="warning" @dismiss-count-down="countDownChanged">
                                    Espera {{ dismissCountDown }} segudos...
                                </b-alert>
                            </div>
                        </b-form>
                    </b-card-text>
                </b-card>
            </b-col>
            <b-col cols="4"></b-col>
        </b-row>
    </b-container>
</div>
</template>

<script>
import restorePass from './restorePass.vue'

export default {
    components:{
        restorePass
    },
    data() {
        return {
            form: {
                id: '',
                contra: ''
            },
            show: true,
            dismissSecs: 5,
            dismissCountDown: 0,
            showDismissibleAlert: false
        }
    },
    //Valida en la vista el tamaño de usuario y password
    computed: {
        nameStateID() {
            return this.form.id.length > 2 ? true : false
        },
        nameStatePass() {
            return this.form.contra.length > 2 ? true : false
        }
    },
    methods: {
        //Evento post valida antes de enviar
        onSubmit(evt) {
            evt.preventDefault()
            if (this.form.id.length > 2 &&
                this.form.contra.length > 2) {
                this.showAlert();
            }
        },
        //Obtene el logo del login del repositorio
        logo() {
            return require("../../assets/impi_logo.png")
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
            if (this.dismissCountDown === 0) {
                var data = JSON.stringify(this.form)
                this.validaUsr(data);
            }
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        },
        validaUsr() {
            this.axios.post('/api/chatbot/auth/login.json', {
                    user: this.form.id,
                    password: this.form.contra
                }, ).then((response) => {
                    console.log(response.data.succeed)
                    if(!response.data.succeed)
                    {
                        this.$bvToast.toast('Inenta de nuevo...', {
                            title: `aviso`,
                            variant: 'warning',
                            toaster: 'b-toaster-bottom-center',
                            solid: true
                        });
                    }
                    else
                    {
                        this.$router.push('/masterUsr')
                    }
                    console.log(response.data);
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
}
</script>
