<template>
    <div>
        <b-form @submit="onSubmit" v-if="show">
      <b-form-group
        id="input-group-1"
        label="Contexto 1"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="form.entrada"          
          required
          placeholder="Contexto Entrada"
        ></b-form-input>
      </b-form-group>
      <b-form-group
        id="input-group-1"
        label="Contexto 2"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="form.salida"          
          required
          placeholder="Contexto Salida"
        ></b-form-input>
      </b-form-group>
      <p></p>
      <b-container class="bv-example-row">
        <b-row>
            <b-col cols="6"></b-col>
            <b-col cols="2">
                <b-button type="submit" variant="success">Actualizar</b-button>                
            </b-col>
            <b-col cols="1">
            </b-col>
            <b-col cols="2">
                <b-button @click="$bvModal.hide('modal-contexto')">Cancelar</b-button>
            </b-col>
        </b-row>        
      </b-container>
    </b-form>
   
   
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          entrada: '',
          salida: ''
        },
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
       this.showConfirm()
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.entrada = ''
       this.form.salida = ''
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      showConfirm() {
        
        this.$bvModal.msgBoxOk('Datos enviados correctamente', {
          title: 'Confirmación',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.$bvModal.hide('modal-contexto')
          })
          .catch(err => {
            // An error occurred
          })
      }
    }
  }


</script>