<template>
  <div>
      <b-modal :id="ModalEditaContext.id" title="EDITAR CONTEXTO" ok-only  hide-footer>
        <editContext></editContext>
      </b-modal>
      <b-table bordered
        show-empty
        small 
        head-variant="light"      
        :items="items"
        :fields="fields">
            <template v-slot:cell(editar)="row">
            <b-button size="sm" @click="infoa(row.item, row.index, $event.target)" class="mr-1">
            Editar
            </b-button>       
            </template>          
        </b-table>         
    </div>
</template>
<script>
import editContext from '../01-Contexto/editaContexto.vue'
export default {
   components:{
     editContext
   },
    data() {
      return {
        items: [
          { ID: 1, contextoIn: 'CONTEXTO 1',contextoOut: 'CONTEXTO 1' },
          { ID: 2, contextoIn: 'CONTEXTO 2' ,contextoOut: 'CONTEXTO 2'},
          { ID: 3, contextoIn: 'CONTEXTO 3' ,contextoOut: 'CONTEXTO 3'},
          { ID: 4, contextoIn: 'CONTEXTO 4' ,contextoOut: 'CONTEXTO 4'}
        ],
        fields: [
            { key: 'ID', label: 'ID ' },
            { key: 'contextoIn', label: 'Contexto Entrada' },
            { key: 'contextoOut', label: 'Contexto Salida' },
            { key: 'editar', label: 'Editar' }            
         ],         
         ModalEditaContext: {
          id: 'modal-contexto',
          title: '',
          content: ''
        }       
      }
    },    
     methods: {
     infoa(item, index, button) {
        this.ModalEditaContext.title = `Row index: ${index}`
        this.ModalEditaContext.content = JSON.stringify(item, null, 2)
        this.$root.$emit('bv::show::modal', this.ModalEditaContext.id, button)
      }
  }
}
</script>