<template>
  <div>
      <b-modal :id="data.name" :title="data.name" ok-only  hide-footer>
        <editaGeneric :data="data"></editaGeneric>
      </b-modal>
      <b-table bordered
        show-empty
        small 
        head-variant="light"      
        :items="data.items"
        :fields="data.fields">
            <template v-slot:cell(editar)="row">
            <b-button size="sm" @click="infoa(row.item, row.index, $event.target)" class="mr-1">
                Editar
            </b-button>       
            </template>          
        </b-table>         
    </div>
</template>
<script>
import editaGeneric from '../03-Generico/edita.vue'
export default {
   props:['data'],
   components:{
     editaGeneric
   },
    data() {
      return {            
         ModalEditaGeneric: {
          id: '',
          title: '',
          content: ''
        }       
      }
    },    
     methods: {
     infoa(item, index, button) {
        this.ModalEditaGeneric.title = `Row index: ${index}`
        this.ModalEditaGeneric.content = JSON.stringify(item, null, 2)
        this.ModalEditaGeneric.id = data.name
        console.log(this.ModalEditaGeneric.id)
        this.$root.$emit('bv::show::modal', data.name, button)
      }
  }
}
</script>
