<template>
<div>
   <b-container class="text-left">
        <b-row>
            <b-col cols="4"></b-col>
            <b-col cols="4">
                <b-card-group deck>
                    <b-card title="" header-tag="header">
                        <template v-slot:header>
                            <h6 class="mb-0">Recupera Contraseña</h6>
                        </template>
                        <b-card-text>
                            <b-form-group id="fieldset-1" description="Aqui debes introducir el token" label="token" label-for="input-1" :invalid-feedback="invalidFeedback" :valid-feedback="validFeedback" :state="state">
                                <b-form-input id="input-1" v-model="name" :state="state" trim></b-form-input>
                            </b-form-group>
                           
                        </b-card-text>
                    </b-card>
                </b-card-group>
           </b-col>
            <b-col cols="4"></b-col>
        </b-row>
    </b-container>
</div>
</template>

<script>
export default {
    computed: {
        state() {
            return this.name.length >= 4 ? true : false
        },
        invalidFeedback() {
            if (this.name.length > 4) {
                return ''
            } else if (this.name.length > 0) {
                return 'mas de 4 caracteres'
            } else {
                return 'Introduce tu token'
            }
        },
        validFeedback() {

                console.log('entro')
               this.axios.get('/api/chatbot/auth/restore-password.json', {
                    securityToken: this.name
                }, ).then((response) => {
                    console.log(response.data.succeed)
                    if(response.data.succeed)
                    {
                        this.$bvToast.toast('', {
                            title: `aviso`,
                            variant: 'warning',
                            toaster: 'b-toaster-bottom-center',
                            solid: true
                        });
                    }
                    console.log(response.data);
                })
                .catch((e) => {
                    console.log(e);
                });
            return this.state === true ? 'Gracias' : ''
        }
    },
    data() {
        return {
            name: ''
        }
    }
}
</script>
